<template>
    <div class="ordersPage">
        <b-tabs nav-class="h4 text-uppercase bg-dark justify-content-center" nav-wrapper-class="stretched" content-class="d-lg-flex">
            <b-tab title="В работе" active class="col-lg-8 p-0 pb-4 overflow-auto">
                <b-container fluid>
                    <div class="p-4 text-center" v-if="!unconfirmed.length && !confirmed.length">
                        <span class="text-muted h4">Актуальные заказы отсутствуют</span>
                    </div>
                    <div class="mb-2 mt-4" v-if="unconfirmed.length">
                        <span class="small text-muted h4 text-uppercase">Новые</span>
                        <b-badge pill variant="danger" class="ml-1">{{ unconfirmed.length }}</b-badge>
                    </div>
                    <div class="bg-white rounded shadow-sm ml-n mr-n m-md-0 ordersPage-list" v-if="unconfirmed.length">
                        <ul class="list-unstyled">
                            <li class="position-relative"
                                v-for="(item, index) in unconfirmed"
                                :class="{'bg-secondary': item.id === order}">
                                <div class="d-md-none pt-3 pr-3 pb-3 pl-md-3 ml-3" :class="{'border-bottom': (index != unconfirmed.length -1)}">
                                    <div class="d-flex justify-content-between mb-1">
                                        <div class="h4 text-muted m-0">№{{ item.id }}</div>
                                        <div class="h4 text-muted m-0">{{ item.date }}</div>
                                    </div>
                                    <div class="h3 m-0">{{ item.phone }} · {{ item.name }}</div>
                                    <div class="text-danger small strong">необходимо подтверждение</div>
                                </div>
                                <div class="d-none d-md-block pt-3 pr-3 pb-3 pl-md-3" :class="{'border-bottom': (index != unconfirmed.length -1)}">
                                    <div class="d-flex justify-content-between m-0">
                                        <div class="h3 m-0 col-4 p-0">№{{ item.id }}</div>
                                        <div class="h3 m-0 col-4 p-0">{{ item.phone }}<span v-if="item.name"> · </span>{{ item.name }}</div>
                                        <div class="h3 m-0 col-4 p-0 text-right">{{ item.date }}</div>
                                    </div>
                                    <div class="text-danger small strong">необходимо подтверждение</div>
                                </div>
                                <router-link :to="{ name: 'Order', params: { id: item.id }}"
                                             tag="div"
                                             class="d-lg-none block-link">
                                </router-link>
                                <b-link class="block-link d-none d-lg-block" @click="loadOrder(item.id)"></b-link>
                            </li>
                        </ul>
                    </div>
                    <div class="mb-2 mt-4" v-if="confirmed.length">
                        <span class="small text-muted h4 text-uppercase">Подтвержденные</span>
                        <b-badge pill variant="success" class="ml-1">{{ confirmed.length }}</b-badge>
                    </div>
                    <div class="bg-white rounded shadow-sm border-bottom ml-n mr-n m-md-0 ordersPage-list" v-if="confirmed.length">
                        <ul class="list-unstyled">
                            <li class="position-relative"
                                v-for="(item, index) in confirmed"
                                :class="{'bg-secondary': item.id === order}">
                                <div class="d-md-none pt-3 pr-3 pb-3 pl-md-3 ml-3" :class="{'border-bottom': (index != confirmed.length -1)}">
                                    <div class="d-flex justify-content-between mb-1">
                                        <div class="h4 text-muted m-0">№{{ item.id }}</div>
                                        <div class="h4 text-muted m-0">{{ item.date }}</div>
                                    </div>
                                    <div class="h3 m-0">{{ item.phone }} · {{ item.name }}</div>
                                </div>
                                <div class="d-none d-md-block pt-3 pb-3 pr-3 pl-md-3 ml-3" :class="{'border-bottom': (index != confirmed.length -1)}">
                                    <div class="d-flex justify-content-between">
                                        <div class="h3 m-0 col-4 p-0">№{{ item.id }}</div>
                                        <div class="h3 m-0 col-4 p-0">{{ item.phone }}<span v-if="item.name"> · </span>{{ item.name }}</div>
                                        <div class="h3 m-0 col-4 p-0 text-right">{{ item.date }}</div>
                                    </div>
                                </div>
                                <router-link :to="{ name: 'Order', params: { id: item.id }}"
                                             tag="div"
                                             class="d-lg-none block-link">
                                </router-link>
                                <b-link class="block-link d-none d-lg-block" @click="loadOrder(item.id)"></b-link>
                            </li>
                        </ul>
                    </div>
                </b-container>
            </b-tab>
            <b-tab title="Архив" class="col-lg-8 p-0 pb-4 overflow-auto">
                <b-container fluid>
                    <div class="p-4 text-center" v-if="!archived.length">
                        <span class="text-muted h4">Заказы в архиве отсутствуют</span>
                    </div>
                    <div class="bg-white rounded shadow-sm border-bottom ml-n mr-n mr-md-0 ml-md-0 mt-4 ordersPage-list" v-if="archived.length">
                        <ul class="list-unstyled">
                            <li class="position-relative"
                                v-for="(item, index) in archived"
                                :class="{'bg-secondary': item.id === order, 'text-muted': item.canceled}">
                                <div class="d-md-none pt-3 pr-3 pb-3 pl-md-3 ml-3" :class="{'border-bottom': (index != archived.length -1)}">
                                    <div class="d-flex justify-content-between mb-1">
                                        <div class="h4 text-muted m-0">№{{ item.id }}</div>
                                        <div class="h4 text-muted m-0">{{ item.date }}</div>
                                    </div>
                                    <div class="h3 m-0 d-inline-block">{{ item.phone }}<span v-if="item.name"> · </span>{{ item.name }}</div>
                                </div>
                                <div class="d-none d-md-block pt-3 pr-3 pb-3 pl-md-3" :class="{'border-bottom': (index != archived.length -1)}">
                                    <div class="d-flex justify-content-between">
                                        <div class="h3 m-0 col-4 p-0">№{{ item.id }}</div>
                                        <div class="h3 m-0 col-4 p-0">{{ item.phone }}<span v-if="item.name"> · </span>{{ item.name }}</div>
                                        <div class="h3 m-0 col-4 text-right p-0">{{ item.date }}</div>
                                    </div>
                                </div>
                                <router-link v-if="!item.canceled"
                                             :to="{ name: 'Order', params: { id: item.id }}"
                                             tag="div"
                                             class="d-lg-none block-link">
                                </router-link>
                                <b-link v-if="!item.canceled" class="block-link d-none d-lg-block" @click="loadOrder(item.id)"></b-link>
                            </li>
                        </ul>
                    </div>
                </b-container>
            </b-tab>
            <div class="col-lg-4 p-0 bg-secondary border-left d-none d-lg-flex align-items-center justify-content-center" v-if="!order">
                <div class="h4 text-muted m-0">заказ не выбран</div>
            </div>
            <div class="col-lg-4 p-0 pt-5 pb-4 bg-secondary border-left d-none d-lg-block overflow-auto" v-if="order">
                <b-container fluid>
                    <div class="h2 text-muted mb-5 strong">Заказ №{{ order.id }}</div>
                    <div class="mb-2 mt-4">
                        <span class="small text-muted h4 text-uppercase">Детали заказа</span>
                    </div>
                </b-container>
                <div class="bg-white border-bottom pt-4 pb-4 pl-4 pr-4">
                    <div class="mb-4">
                        <b-dropdown variant="info" text="Изменить статус" no-caret right class="float-right">
                            <b-dropdown-item
                                    v-for="(status, id) in orderStatuses"
                                    :active="id == order.status" :disabled="id == order.status"
                                    @click="openChangeStatusModal(id)">
                                {{ status }}
                            </b-dropdown-item>
                        </b-dropdown>
                        <div class="h4 mb-1 text-muted">Статус заказа</div>
                        <div class="h3 m-0">{{ getStatusName(order.status) }}</div>
                    </div>
                    <div class="mb-4">
                        <div class="h4 mb-1 text-muted">Информация о заказчике</div>
                        <div class="h3 m-0">{{ order.phone }}<span v-if="order.name.length"> · </span>{{ order.name }}</div>
                    </div>
                    <div>
                        <div class="h4 mb-1 text-muted">Дата и время</div>
                        <div class="h3 m-0">{{ order.date }}</div>
                    </div>
                </div>
                <b-container fluid>
                    <div class="mb-2 mt-4">
                        <span class="small text-muted h4 text-uppercase">
                            <span v-word-ends:позиция|позиции|позиций="order.positions.length"></span>
                            · на сумму
                            <span v-price-format="getTotalPrice(order.positions)"></span>
                        </span>
                    </div>
                </b-container>
                <div class="bg-white border-bottom pt-1 pb-1 pl-4 pr-4">
                    <ul class="list-unstyled">
                        <li class="pt-2 pb-2" v-for="position in order.positions">
                            <div class="h3 mb-1">{{ position.name }}</div>
                            <div class="d-flex justify-content-between">
                                <div class="h4 text-muted m-0">
                                    <span v-price-format="position.price"></span> × {{ position.count }} {{ position.units }}
                                </div>
                                <div class="h4 text-muted m-0">
                                    <span v-price-format="getPositionPrice(position.price, position.count)"></span>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="pt-4 text-center">
                    <b-button variant="danger" @click="openCancelOrderModal">
                        <c-icon name="cancel" class="align-middle"/>
                        <span class="align-middle">Отменить заказ</span>
                    </b-button>
                </div>
            </div>
        </b-tabs>
        <b-modal v-model="cancelOrderModalVisible" hide-header-close centered>
            <div slot="modal-header">
                <div class="h2 strong">Отменить этот заказ?</div>
            </div>
            <div class="h3 text-muted">Это действие необратимо. Вы уверены, что хотите отменить заказ?</div>
            <div slot="modal-footer">
                <b-button variant="info" @click="closeCancelOrderModal">Нет</b-button>
                <b-button variant="danger" @click="cancelOrder">Отменить заказ</b-button>
            </div>
        </b-modal>
        <b-modal v-model="changeStatusModalVisible" hide-header-close centered>
            <div slot="modal-header">
                <div class="h2 strong">Изменить статус?</div>
            </div>
            <div class="h3 text-muted">Изменить статус этого заказа на «{{ getStatusName(temporaryOrderStatus) }}»?</div>
            <div slot="modal-footer">
                <b-button variant="info" @click="closeStatusChangeModal()">Отмена</b-button>
                <b-button variant="danger" @click="changeStatus()">Изменить статус</b-button>
            </div>
        </b-modal>
    </div>
</template>

<script>
  import RouteHelper from '../../mixins/RouteHelper.js';
  import FetchHelper from '../../mixins/FetchHelper.js';

  export default {
    name: 'orders',
    mixins: [FetchHelper, RouteHelper],
    data() {
      return {
        fetchUrl: '/api/orders',
        orderUrl: '/api/order',
        changeStatusUrl: '/api/change_order_status',
        cancelOrderUrl: '/api/cancel_order',
        searchOrdersUrl: '/api/search_orders',

        cancelOrderModalVisible: false,
        changeStatusModalVisible: false,
        temporaryOrderStatus: null,

        confirmed: [],
        unconfirmed: [],
        archived: [],

        order: null,
      }
    },

    computed: {
      orderStatuses() {
        return this.$store.getters['global/orderStatuses'];
      },
      searchResults() {
        return this.$store.getters['global/searchResults'];
      }
    },

    watch: {
      searchResults(data) {
        this.confirmed = [];
        this.unconfirmed = [];
        this.archived = [];
        this.order = null;

        if (!data) {
          this.fetchData();
          return;
        }

        data.forEach(item => {
          let list = this.getOrderListByStatus(item.status);
          list.push(item);
        });
      }
    },

    methods: {
      prepareData(data) {
        if (!data) return;

        data.forEach(item => {
          let list = this.getOrderListByStatus(item.status);
          list.push(item);
        });
      },

      loadOrder(id) {
        if (this.order && this.order.id === id) return;

        this.axios.get(`${this.orderUrl}/${id}`, { headers: { Authorization: this.$store.getters['global/authorizationToken'] }  })
          .then(response => {
            this.isLoadDisabled = false;
            this.order = response.data;
          })
          .catch(e => {
            this.errors.push(e);
            this.isLoadDisabled = false;
          });
      },

      openChangeStatusModal(status) {
        this.changeStatusModalVisible = true;
        this.temporaryOrderStatus = status;
      },

      closeStatusChangeModal() {
        this.changeStatusModalVisible = false;
      },

      changeStatus() {
        this.axios.post(`${this.changeStatusUrl}/${this.order.id}`, {
          headers: { Authorization: this.$store.getters['global/authorizationToken'] },
          data: { status: this.temporaryOrderStatus },
        })
          .then(response => {
            this.isLoadDisabled = false;
            this.closeStatusChangeModal();

            let oldOrderList = this.getOrderListByStatus(this.order.status);
            let newOrderList = this.getOrderListByStatus(this.temporaryOrderStatus);

            const oldArrayIndex = this.getOrderIndex(oldOrderList, this.order.id);
            if (oldArrayIndex === -1) return;

            let item = oldOrderList.splice(oldArrayIndex, 1)[0];
            newOrderList.push(item);
            newOrderList.sort((a, b) => a.id > b.id);

            this.order.status = this.temporaryOrderStatus;
            this.temporaryOrderStatus = null;
          })
          .catch(e => {
            this.errors.push(e);
            this.isLoadDisabled = false;
          });
      },

      openCancelOrderModal() {
        this.cancelOrderModalVisible = true;
      },
      
      closeCancelOrderModal() {
        this.cancelOrderModalVisible = false;
      },

      cancelOrder() {
        this.axios.post(`${this.cancelOrderUrl}/${this.order.id}`, {
          headers: { Authorization: this.$store.getters['global/authorizationToken'] },
        })
          .then(response => {
            this.isLoadDisabled = false;
            this.closeCancelOrderModal();

            let oldOrderList = this.getOrderListByStatus(this.order.status);
            let newOrderList = this.archived;

            const oldArrayIndex = this.getOrderIndex(oldOrderList, this.order.id);
            if (oldArrayIndex === -1) return;

            let item = oldOrderList.splice(oldArrayIndex, 1)[0];
            item.canceled = true;
            newOrderList.push(item);
            newOrderList.sort((a, b) => a.id > b.id);

            this.order = null;
          })
          .catch(e => {
            this.errors.push(e);
            this.isLoadDisabled = false;
          });
      },

      getOrderListByStatus(status) {
        if (status == 0) {
          return this.unconfirmed;
        }

        if (status == 1) {
          return this.confirmed;
        }

        if (status == 2) {
          return this.archived;
        }
      },

      getOrderIndex(list, id) {
        return list.map(item => item.id).indexOf(id);
      },

      getTotalPrice(positions) {
        return positions.reduce((prevPosition, curPosition)=> prevPosition + this.getPositionPrice(curPosition.price, curPosition.count), 0);
      },

      getPositionPrice(price, count) {
        return parseFloat(price) * parseFloat(count);
      },
      
      getStatusName(id) {
        if (!this.orderStatuses) return '';
        return this.orderStatuses[id];
      }
    },

    mounted() {
      this.setTitle('Заказы');
      this.setEnableHeaderSearch(this.searchOrdersUrl, 'Поиск по заказам');
    }
  }
</script>

<style lang="scss">
    @import '../../bootconf.scss';

    .ordersPage {
        &-list {
            li {
                transition: background-color .3s ease;
            }

            li:not(.text-muted):hover {
                background-color: rgba(153, 153, 153, .1);
            }

            li.text-muted {
                cursor: default;

                > div > div {
                    position: relative;

                    &:before {
                        position: absolute;
                        content: '';
                        height: 1px;
                        background-color: rgba(0, 0, 0, 0.2);
                        left: 0;
                        right: 0;
                        top: 0;
                        bottom: 0;
                        margin: auto;
                    }
                }
            }
        }

        .tabs {
            display: flex;
            flex-direction: column;
            height: calc(100vh - 56px);

            @media (min-width: map-get($grid-breakpoints, md)) {
                height: calc(100vh - 64px);
            }

            & .tab-content {
                flex: 1;
            }
        }
    }
</style>


