import Vue from 'vue';
import Router from 'vue-router';

import Demo from '@/components/view/_Demo';
import Orders from '@/components/view/Orders';
import Order from '@/components/view/Order';

Vue.use(Router);

let router = new Router({
  mode: 'history',
  routes: [

    {
      path: '*',
      redirect: "/orders"
    },

    {
      path: '/',
      redirect: '/orders'
    },

    {
      path: '/demo',
      name: 'Demo',
      component: Demo
    },

    {
      path: '/orders',
      name: 'Orders',
      component: Orders,
    },

    {
      path: '/order/:id',
      name: 'Order',
      component: Order,
    },

  ]
});

router.beforeEach((to, from, done) => {
  to.meta.backUrl = from.path;
  done();
});

router.afterEach((to, from) => {
  window.scrollTo(0, 0);
});

export default router;


