import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const global = {
    namespaced: true,
    state: {
      authorizationToken: null,
      orderStatuses: [],
      searchResults: null,
    },
    mutations: {
      setAuthorizationToken(state, data) {
        state.authorizationToken = data;
      },
      setOrderStatuses(state, data) {
        state.orderStatuses = Object.assign(data, state.orderStatuses);
      },
      setSearchResults(state, data) {
        state.searchResults = data;
      }
    },
    getters: {
      authorizationToken(state) {
        return state.authorizationToken;
      },
      orderStatuses(state) {
        return state.orderStatuses;
      },
      searchResults(state) {
        return state.searchResults;
      }
    }
};

const store = new Vuex.Store({
    modules: { global }
});

export default {
    store,
    install (Vue, options) {
        Vue.prototype.$store = store
    }
}


