<template>
    <header class="header bg-dark" :class="{'active': enableSearch && activeSearch}">
        <b-container fluid>
            <div class="header-nav text-light">
                <router-link :to="backUrl || ''"
                             tag="div"
                             class="header-control"
                             v-show="(!homePage || !backUrl) && !activeSearch">
                    <c-icon name="back"/>
                </router-link>
                <div class="header-title" v-show="!activeSearch">
                    <h1 class="mb-0 h2 strong">{{ title }}</h1>
                </div>
                <div class="header-search text-light" v-if="enableSearch && activeSearch">
                    <b-form-group label-for="search" class="m-0">
                        <b-form-input id="search"
                                      type="text"
                                      required
                                      v-model="search"
                                      v-focus
                                      autocomplete="off"
                                      autofocus
                                      :placeholder="searchPlaceholder">
                        </b-form-input>
                    </b-form-group>
                    <span @click="toggleSearch" class="afake text-light">
                        <c-icon name="clear"/>
                    </span>
                </div>
                <div class="header-control"
                     v-show="enableSearch && !activeSearch"
                     @click="toggleSearch">
                    <c-icon name="search"/>
                </div>
            </div>
        </b-container>
    </header>
</template>


<script>
    export default {
        name: 'c-header',
        props: ['title', 'backUrl', 'subtitle', 'homePage', 'enableSearch', 'searchPlaceholder', 'searchUrl'],
        data() {
          return {
            activeSearch: false,
            search: '',
            searchDelay: 500,
            searchTimeout: null,
          }
        },
        methods: {
          toggleSearch() {
            this.activeSearch = !this.activeSearch;
            this.search = '';
            this.$store.commit('global/setSearchResults', null);
          }
        },
      watch: {
        enableSearch: function (val) {
            if (val) return;
            this.activeSearch = false;
        },
        search: function (val) {
            clearTimeout(this.searchTimeout);

            if (val === '') {
              this.$store.commit('global/setSearchResults', null);
            }

            if (val.length < 3) {
                return;
            }

            this.searchTimeout = setTimeout(() => {
                this.axios.get(`${this.searchUrl}/${val}`, {
                    headers: { Authorization: this.authorizationToken },
                })
                .then(response => {
                    this.$store.commit('global/setSearchResults', response.data);
                    this.isLoadDisabled = false;
                })
                .catch(e => {
                    this.errors.push(e);
                    this.isLoadDisabled = false;
                });
            }, this.searchDelay);
        }
      }
    }
</script>

<style lang="scss">
    @import '../../bootconf.scss';

    .header {
        position: relative;
        box-shadow: none;
        z-index: 10;

        &.active {
            background-color: #3f515b !important;
        }

        &-nav {
            position: relative;
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 56px;

            @media (min-width: map-get($grid-breakpoints, md)) {
                height: 64px;
            }
        }

        &-control {
            position: relative;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: transparent;
            vertical-align: middle;
            cursor: pointer;
            color: $white !important;

            &:first-child {
                left: -8px;
                margin-right: 16px;
            }

            &:last-child {
                right: -8px;
            }

            .icon {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
            }

            &:hover,
            &:active {
                background-color: rgba(255, 255, 255, 0.15);
                box-shadow: none;
            }
        }

        &-title {
            flex: 1;
        }

        &-search {
            .icon {
                opacity: .47;
                position: absolute;
                right: 0;
                top: 0;
                bottom: 0;
                margin: auto;
            }

            .form-group {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }

            .form-control {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                font-size: $h2-font-size;
                line-height: 1.4;
                color: $white;
                border: none !important;
                padding-right: 50px;

                &::placeholder {
                    color: rgba(255, 255, 255, .75);
                }
            }
        }
    }
</style>


