export default {
  data() {
    return {
      title: '',
      backUrl: '',
      homePage: false,
      enableHeaderSearch: false,
      searchPlaceholder: '',
      searchUrl: '',
    }
  },
  methods: {
    initConfig() {
      this.title = this.$route.meta.title;
      this.backUrl = this.$route.meta.backUrl;

      this.homePage = this.$route.name === 'Orders';
      this.enableHeaderSearch = this.$route.meta.enableHeaderSearch;
      this.searchPlaceholder = this.$route.meta.searchPlaceholder;
      this.searchUrl = this.$route.meta.searchUrl;
    },

    setTitle(title) {
      this.$route.meta.title = title;
      document.title = this.$route.meta.title;
    },

    setBackUrl(url, useHistory = false) {
      if (!useHistory) {
        this.$route.meta.backUrl = url;
        return;
      }

      this.$route.meta.backUrl = this.$route.meta.backUrl === '/' ? url : this.$route.meta.backUrl;
    },

    setEnableHeaderSearch(url, placeholder) {
      this.$route.meta.enableHeaderSearch = true;
      this.$route.meta.searchUrl = url;
      this.$route.meta.searchPlaceholder = placeholder;
    },

    showHeaderClose() {
      this.$route.meta.showHeaderClose = true;
    },
  },

  mounted() {
    this.initConfig();
  },

  updated() {
    this.initConfig();
  }
}


