<template>
  <div id="app" v-device-touch>
      <c-header :title="title"
                :backUrl="backUrl"
                :homePage="homePage"
                :enableSearch="enableHeaderSearch"
                :searchUrl="searchUrl"
                :searchPlaceholder="searchPlaceholder"/>
    <main role="main">
        <router-view/>
    </main>
  </div>
</template>

<script>
import FetchHelper from './mixins/FetchHelper.js';
import RouteHelper from './mixins/RouteHelper.js';

export default {
  name: 'app',
  mixins: [FetchHelper, RouteHelper],
  data() {
    return {
      fetchUrl: '/api/config',
    };
  },
  methods: {
    prepareData(data) {
        if (!data) return;

        this.$store.commit('global/setOrderStatuses', data.orderStatuses);
    },
  },
  beforeMount() {
    this.$store.commit('global/setAuthorizationToken', this.$route.query.token);
  },
}
</script>

<style lang="scss">
  @import 'bootconf.scss';
  @import '~bootstrap/scss/bootstrap';

  #app {
      min-height: 100vh;
  }
</style>


