export default {
  data() {
    return {
      isLoadDisabled: false,
      errors: []
    }
  },
  methods: {
    fetchData(done) {
      let url = this.fetchUrl;
      const paramsId = this.$route.params.id;
      const authorizationToken = this.$store.getters['global/authorizationToken'];

      if (!url) return;

      if (this.useParamsId) {
        url += '/' + paramsId;
      }

      let cached = this.$ls.get(url);
      if (cached) {
        // this.prepareData(cached);
      }

      this.isLoadDisabled = true;

      this.axios.get(url, {
        headers: {
          Authorization: authorizationToken,
        }
      })
        .then(response => {
          if (typeof done === 'function') done();
          this.isLoadDisabled = false;

          this.prepareData(response.data);
          //this.$ls.set(url, response.data);
        })
        .catch(e => {
          if (typeof done === 'function') done();

          this.errors.push(e);
          this.isLoadDisabled = false;
        });
    },
    prepareData(data) {},
  },
  mounted() {
    this.fetchData();
    this.$parent.$on('pullToRefresh:refresh', (done) => {
      this.fetchData(done);
    })
  },
}


